import Vue from 'vue';
import Vuex from 'vuex';



Vue.use(Vuex);


export default new Vuex.Store({
    state:{
        openDialog:false
    },
    getters:{
        getDialogState:(state)=>{
            return state.openDialog
        }
    },
    mutations:{
        changeDialogState(state){
            state.openDialog = ! state.openDialog
        }
    },
    actions:{
        changeDialogState({commit}){
            commit("changeDialogState")
        }
    }

})