import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import store from '@/store/store';
import FlashMessage from '@smartweb/vue-flash-message';


Vue.config.productionTip = false
Vue.use(FlashMessage);

new Vue({
  vuetify,
  store,
  render: h => h(App)
}).$mount('#app')
