<template>
  <v-card>
      <!-- <FlashMessage ></FlashMessage> -->

    <v-card-title>


      <v-text-field
        v-model="search"
        append-icon="mdi-search"
        label="Search for a room, specific time, etc."
        single-line
        hide-details
      ></v-text-field>

    </v-card-title>

    <v-data-table
      :headers="headers"
      :items="rooms"
      :search="search"
      sort-by='isEmpty'
      :sort-desc= true
      :loading="loading_data"

      loading-text="Loading data, Please be patient..."
    >
    </v-data-table>


  </v-card>
</template>


<script>
import axios from 'axios';

export default {
    name:"Table",
     data () {
      return {
        loading_data : true,
        search: '',
        headers: [
          {
            text: 'Room',
            align: 'left',
            sortable: true,
            value: 'room',
            
          },
          { text: 'Empty', value: 'isEmpty' ,align:"center"},
          { text: 'Empty in (mins)', value: 'emptyIn' ,align:"center"},
          { text: 'Empty for (mins)', value: 'emptyFor',align:"center" },


        ],
        rooms: []
      }
    },
    computed:{
      dayName () {
          var dayNames = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'];
          return dayNames[new Date().getDay()-1];
      },},
      
    async created (){

      this.info = await axios.get('https://floating-harbor-45237.herokuapp.com/getRooms?Day='+this.dayName)
      .then(response => (this.rooms =(JSON.parse(response['data']))))
      .catch(err=>window.console.log(err));
      this.loading_data = false;
      
    }

    

}
</script>
